<template>
    <div>
        <!--  -->
        <Modal
                :title="dataFormTitle"
                v-model="dataFormShow"
                :width="modelWidth"
                class-name="vertical-center-modal"
                :styles="{top: '20px',height: modelHeight,zIndex:100}"
                draggable
        >
            <div slot="header">
                <h3>{{ dataFormTitle }}</h3>
            </div>
            <Form :label-width="formLabelWidth" :model="dataForm"  :ref="refName"  :rules="ruleValidate">
                <template v-for="(item,index) in fieldsData">
                    <template v-if="item.data_form == true">
                        <template v-if="item.data_type == 'hidden'">
                            <input type="hidden" v-model="dataForm[item.slot]" >
                        </template>
                        <template v-else>
                            <Row>
                                <FormItem :label="item.title"  :prop="item.slot" >
                                    <Select @on-change="selectEvent(item,dataForm[item.slot])" style="width:200px"  v-model="dataForm[item.slot]" filterable  placeholder="" v-if=" item.data_type == 'select' ">
                                        <template v-for="(option_item,option_index) in item.options">
                                            <Option v-if=" /^\d+$/.test(option_index) "
                                                    :key="option_index"
                                                    :value="parseInt(option_index)">{{ option_item }}</Option>
                                            <Option v-else-if=" /^\d+\.+\d+$/.test(option_index) "
                                                      :key="option_index"
                                                      :value="parseFloat(option_index)">{{ option_item }}</Option>
                                            <Option v-else
                                                      :key="option_index"
                                                      :value="option_index">{{ option_item }}</Option>
                                        </template>

                                    </Select>

                                    <DatePicker
                                            v-else-if=" item.data_type == 'date' "
                                            v-model="dataForm[item.slot]"
                                            type="date"
                                            format="yyyy-MM-dd"
                                            placeholder=""
                                            @on-change="(datetime) =>{ changeDatetime(datetime,item.slot)}"
                                            @on-open-change="dataFormShow = true"
                                    >
                                    </DatePicker>

                                    <DatePicker
                                            v-else-if=" item.data_type == 'datetime' "
                                            v-model="dataForm[item.slot]"
                                            type="datetime"
                                            format="yyyy-MM-dd HH:mm:ss"
                                            placeholder=""
                                            @on-change="(datetime) =>{ changeDatetime(datetime,item.slot)}"
                                            @on-open-change="dataFormShow = true"
                                    >
                                    </DatePicker>

                                    <vc-upload @on-upload-success="uploadSuccess" :upload-file-server="uploadFileServer" :extension="item.extension"  :upload-file-max-size="uploadFileMaxSize"  :ref="item.slot" :prop="item.slot"  v-else-if=" item.data_type == 'image' || item.data_type == 'img' || item.data_type == 'file' "></vc-upload>

                                    <vue-neditor-wrap  v-else-if=" item.data_type == 'editor' "
                                            v-model="dataForm[item.slot]"
                                            :config="editorConfig"
                                            :destroy="false"
                                            @ready="editorReady">
                                    </vue-neditor-wrap>

                                    <RadioGroup  v-else-if=" item.data_type == 'radio' "  v-model="dataForm[item.slot]">
                                        <template v-for="(option_item,option_index) in item.options">
                                            <Radio v-if=" /^\d+$/.test(option_index) " :label="parseInt(option_index)"><span>{{ option_item }}</span></Radio>
                                            <Radio v-else-if=" /^\d+\.+\d+$/.test(option_index) " :label="parseFloat(option_index)"><span>{{ option_item }}</span></Radio>
                                            <Radio v-else  :label="option_index"><span>{{ option_item }}</span></Radio>
                                        </template>
                                    </RadioGroup>

                                    <Input v-model="dataForm[item.slot]" type="textarea" :rows="4" :placeholder="'' + item.title" v-else-if=" item.data_type == 'textarea' "></Input>

                                    <Input v-model="dataForm[item.slot]" :placeholder="'' + item.title" v-else=" item.data_type == 'string' "></Input>

                                </FormItem>

                            </Row>
                        </template>

                    </template>


                </template>
            </Form>
            <div slot="footer" class="dialog-footer">
                <Button  type="default" @click="resetDataForm"></Button>
                <Button type="primary" @click="saveDataForm()"></Button>
            </div>

        </Modal>

    </div>
</template>

<style>
    .ivu-modal-content{ height: 100% !important;}
    .ivu-modal-body{ height: calc(100% - 110px) !important;}
    .ivu-modal-body{ overflow: auto ;}
    .ivu-input-wrapper{ width: 98% !important;}
</style>

<script>

    import VueNeditorWrap from 'vue-neditor-wrap'
    import VcUpload from './upload.vue'


    export default {
        name:'vc-form',
        props:['dataFormTitle','modelWidth','modelHeight','formLabelWidth','dataForm','ruleValidate','fieldsData','refName','uploadFileServer','uploadFileMaxSize','editorConfig','token'],
        components:{VcUpload,VueNeditorWrap},
        data(){
            return {
                editorCfg: {
                    // ,
                    serverUrl: '',
                    // UEditor,index.html
                    UEDITOR_HOME_URL: '',
                    // 
                    autoHeightEnabled: false,
                    // 
                    initialFrameHeight: 240,
                    // 
                    initialFrameWidth: '100%',
                    // 
                    enableAutoSave: false,
                    zIndex: 1800
                },
                dataFormShow: false
            }
        },
        updated:function () {
            let that = this
            that.fieldsData.forEach(function (val,key) {
                if(val.data_type == 'date' ||  val.data_type == 'datetime'){
                    //iviewDatePickerTZ
                    that.dataForm[val.slot] = that.getFormatDate(that.dataForm[val.slot])
                }
            })

        },
        mounted: function () {
            let that = this
            that.editorCfg = that.editorConfig
            that.editorCfg.serverUrl = that.uploadFileServer
            console.log(that.editorCfg)

            //
            console.log(that.fieldsData)

        },
        methods:{
            editorReady(text){
                console.log(text)

            },

            //
            selectEvent(item,sel_val,showDlg){
                let that = this
                if(showDlg == undefined) showDlg = true
                if(showDlg == true) that.dataFormShow = true
                console.log(item)
                if(item.filter_form_name != '' && item.filter_field != '' && item.filter_url != ''){
                    //
                    that.fieldsData.forEach(function (v,k) {
                        if(v.slot == item.filter_form_name){  //
                            // v.field_id  ID 
                            // item.filter_field  
                            // sel_val 
                            let tokenParam = ''
                            if(that.token != '') tokenParam = '?token=' + that.token
                            that.$parent.post(item.filter_url + tokenParam,{field_id:v.field_id,filter_field:item.filter_field,sel_val:sel_val}).then(function (data) {
                                v.options = data.data.data
                            });

                           // console.log(v.field_id)
                            //v.options = ''
                        }
                    })
                }
            },
            uploadSuccess: function (uploadList,prop) {
                let fileList = []
                uploadList.forEach(function (v,k) {
                    fileList.push(v.url)
                })
                console.log(fileList)
                this.dataForm[prop] = fileList.join(',')
            },
            //iviewDatePickerTZ
            getFormatDate: function(dateStr){
                let dateJson = new Date(dateStr).toJSON()
                let dateRes = new Date(+ new Date(dateJson) + 8 * 3600 * 1000).toISOString().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'')
                return dateRes
            },
            //
            resetDataForm: function () {
                this.$refs[this.refName].resetFields()
            },
            //
            saveDataForm: function () {
                this.$emit('on-save-data-form',this.dataForm)
            },
            //
            changeDatetime: function (datetime, prop) {
                this.dataForm[prop] = datetime
                this.dataFormShow = true
            },
        }
    }
</script>