<template>
    <div>
        <div class="vc-upload-list" v-for="item in uploadList">
            <template v-if="item.status === 'finished'">
                <img :src="item.full_url">
                <div class="vc-upload-list-cover">
                    <Icon type="ios-eye-outline" title="" @click.native="handleView(item)"></Icon>
                    <Icon type="ios-trash-outline" title="" @click.native="handleRemove(item)"></Icon>
                </div>
            </template>
            <template v-else>
                <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
            </template>
        </div>
        <Upload
                ref="upload"
                :show-upload-list="false"
                :default-file-list="defaultList"
                :on-success="handleSuccess"
                :format="extension"
                :max-size="uploadFileMaxSize"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                :before-upload="handleBeforeUpload"
                multiple
                type="drag"
                :action="uploadFileServer"
                style="display: inline-block;width:58px;">
            <div style="width: 58px;height:58px;line-height: 58px;">
                <Icon type="md-add" size="20"></Icon>
            </div>
        </Upload>
        <Modal title="" v-model="visible" :z-index="1810">
            <img :src="viewCurrentFile" v-if="visible" style="width: 100%">
        </Modal>
    </div>
</template>

<script>
    export default {
        name: 'vc-upload',
        props:['uploadServer','prop','uploadFileServer','extension','uploadFileMaxSize'],
        data(){
            return {
                defaultList: [],
                viewCurrentFile: '',
                visible: false,
                uploadList: []
            }
        },
        methods: {
            handleView (file) {
                this.viewCurrentFile = file.full_url;
                this.visible = true;
            },
            handleRemove (file) {
                this.uploadList.splice(this.uploadList.indexOf(file), 1);
            },
            handleSuccess (response, file, fileList) {
                let file_list = [];
                fileList.forEach(function (item,index) {
                    file_list.push({
                        name: item.name,
                        percentage: item.percentage,
                        showProgress: item.showProgress,
                        size: item.size,
                        status: item.status,
                        full_url: item.response.full_url,
                        url: item.response.url
                    })
                })
                this.uploadList = file_list

                this.$emit('on-upload-success', this.uploadList, this.prop)
            },
            handleFormatError (file) {
                this.$Notice.warning({
                    title: '',
                    desc: 'File format of ' + file.name + ' is incorrect, please select '+ this.extension.join(',') +'.'
                });
            },
            handleMaxSize (file) {
                this.$Notice.warning({
                    title: 'Exceeding file size limit',
                    desc: 'File  ' + file.name + ' is too large, no more than 2M.'
                });
            },
            handleBeforeUpload () {
                const check = this.uploadList.length < 10;
                if (!check) {
                    this.$Notice.warning({
                        title: 'Up to ten pictures can be uploaded.'
                    });
                }
                return check;
            }
        },
        updated: function(){
           // this.uploadList = this.$refs.upload.fileList
        },
        mounted: function(){

            //this.uploadList = this.$refs.upload.fileList
        }
    }
</script>

<style>
    .vc-upload-list{
        display: inline-block;
        width: 60px;
        height: 60px;
        text-align: center;
        line-height: 60px;
        border: 1px solid transparent;
        border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 4px;
    }
    .vc-upload-list img{
        width: 100%;
        height: 100%;
    }
    .vc-upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
    }
    .vc-upload-list:hover .vc-upload-list-cover{
        display: block;
    }
    .vc-upload-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
</style>